/*方法说明
 *@method 方法名
 *@param{obj}参数名 参数说明 {}
 *@param{item}参数名 参数说明 {value: '中文', key: 'df'}
 *@return {df: '中文'}加到obj上，如果key重复，则使用df1，类推
*/
export function extendObj(obj, item) {
  const { key, value } = item
  let suffix = 0
  while (obj[key + (suffix ? suffix : '')]) {
    suffix++;
  }
  const last_key = key + (suffix === 0 ? '' : suffix)
  obj[last_key] = value
  return last_key
}


