<template>
  <div class="buffer-wrapper">
    <Table :columns="columns" :data="data">
      <p slot-scope="{ row, index }" slot="name">
      </p>
      <template slot-scope="{ row, index }" slot="age">
        {{ row.age }}
      </template>
    </Table>
    <!-- <Card>
      <div slot='title'>title</div>
      <div>
        <div slot='title'>title11</div>
      </div>
      <test x='测试slot-scope'>
        <template v-slot:default='{x, y}'>wew
          x:{{x}}{{y}}
        </template>
      </test>
      <Descriptions title='测试Descriptions'>
        <DescriptionItem label='星期五'>明天是星期六</DescriptionItem>
        <DescriptionItem label='星期df'>明天是星期六</DescriptionItem>
        <DescriptionItem label='星期fdf五'>明天是e星期六</DescriptionItem>
        <DescriptionItem label='星期五'>明天是星期f六</DescriptionItem>
        <DescriptionItem label='星期vfcv五'>明天是fd星期六</DescriptionItem>
        <DescriptionItem label='星期vc五'>明天是星期六</DescriptionItem>
        <DescriptionItem label='星期vb五'>明天是星期六</DescriptionItem>
        <DescriptionItem label='星期五d'>明天是星期六</DescriptionItem>
      </Descriptions>
    </Card> -->
    <div class="buffer-left">
      <Input v-model="custom_input" type="textarea" placeholder="Enter something..." :rows="16"/>
      <div class="left-button-wrapper">
        <Button type="primary" @click="clear">清空</Button>
        <Button type="primary" @click="buffer2string">Buffer转成string</Button>
        <Button type="primary" @click="uni2zh">Unicode转中文</Button>
      </div>
    </div>
    <div class="buffer-right">
      <Icon type="ios-arrow-back" class="back-arrow" size="20" @click="shift2left"/>
      <Input v-model="custom_output" type="textarea" placeholder="result..." :rows="16"/>
    </div>
  </div>
</template>

<script>
import localforage from 'localforage'
import PageApi from '../apis/PageApi'
import test from '@/components/test'
export default {
  components: {
    test
  },
  data () {
    return {
      custom_input: '',
      custom_output: '',
      editIndex: -1,  // 当前聚焦的输入框的行数
      editName: '',
      columns: [
          {
            title: '姓名',
            slot: 'name'
          },
          {
            title: '年龄',
            slot: 'age'
          },
          {
            title: '操作',
            slot: '',
            key: 'action'
          }
        ],
        data: [
          {
            name: '王小明',
            age: 18,
            birthday: '919526400000',
            address: '北京市朝阳区芍药居'
          },
          {
            name: '张小刚',
            age: 25,
            birthday: '696096000000',
            address: '北京市海淀区西二旗'
          },
        ],
    };
  },
  async created() {
    let pageApi = new PageApi()
    // pageApi.addPage({data:{a:12112}}).then().catch(e=> {console.error(e, 11111)})
    // pageApi.addPage({data:{a:333333}})
    // pageApi.addPage({data:{a:333333}})
    // pageApi.deletePage('54b70ee4-b472-470f-9992-e884a01000ba')
    // console.log(await pageApi.getAllPages())
    // var page = localforage.createInstance({
    //   name: "page"
    // }).then((a) => {console.log(a)}).catch((e)=> {e});
    // console.log(await page.keys())
  },
  methods: {
    clear(){
      this.custom_input = ''
      this.custom_output = ''
      let re = /'(this.\$l\('\w+'\))'/g;
      let str = "[{'name':'a','label':'this.$l('name')'},{'name':'d','label':'this.$l('path')'}]";
      console.log(str.search(re))
      let x = str.replace(re, (all, $1) => {
        console.log($1, '111111');
        return $1;
      })
      console.log(x, 'xxxxxxxxxx');
    },
    buffer2string () {
      if(this.custom_input === ''){
        this.$Message.error('请输入buffer数组(十进制)！')  // JSON.parse解析其它进制数组出错
        return
      }
      try{
        let buffer = new Buffer(JSON.parse(this.custom_input)) //[0xE4,0xB8,0xAD]
        this.custom_output = buffer.toString()
        console.log(this.custom_output)
      }catch(e){
        this.custom_output = ''
        this.$Message.error('请输入buffer数组(十进制)！')
      }
      
    },
    shift2left(){
      this.custom_input = this.custom_output
      this.custom_output = ''
    },
    uni2zh(){
      // this.custom_output = decodeURI(encodeURI(this.custom_input))
      this.custom_output = this.decodeUnicode(this.custom_input)
    },
    decodeUnicode (str) {
      str = str.replace(/\\/g, '%')
      return unescape(str)
    }
  }
}
</script>

<style scoped>
.buffer-wrapper{
  padding: 20px;
  display: flex;
}
.buffer-left{
  flex: 1;
  margin-right: 100px;
}
.left-button-wrapper{
  margin-top: 20px;
}
.buffer-right{
  position: relative;
  flex: 1;
}
.back-arrow{
  position: absolute;
  top: 150px;
  left: -60px;
  padding: 5px;
  background: rgb(175, 172, 172);
}
</style>


