<template>
  <div>
     <Collapse v-model="value1">
        <Panel name="1">
            史蒂夫·乔布斯
            <p slot="content">史蒂夫·乔布斯（Steve Jobs），1955年2月24日生于美国加利福尼亚州旧金山，美国发明家、企业家、美国苹果公司联合创办人。</p>
        </Panel>
        <Panel name="2" hideArrow>
            斯蒂夫·盖瑞·沃兹尼亚克
            <p slot="content">斯蒂夫·盖瑞·沃兹尼亚克（Stephen Gary Wozniak），美国电脑工程师，曾与史蒂夫·乔布斯合伙创立苹果电脑（今之苹果公司）。斯蒂夫·盖瑞·沃兹尼亚克曾就读于美国科罗拉多大学，后转学入美国著名高等学府加州大学伯克利分校（UC Berkeley）并获得电机工程及计算机（EECS）本科学位（1987年）。</p>
        </Panel>
        <Panel name="3">
            乔纳森·伊夫
            <p slot="content">乔纳森·伊夫是一位工业设计师，现任Apple公司设计师兼资深副总裁，英国爵士。他曾参与设计了iPod，iMac，iPhone，iPad等众多苹果产品。除了乔布斯，他是对苹果那些著名的产品最有影响力的人。</p>
        </Panel>
    </Collapse>
    <p v-if="false">33434</p>
    <test ref="test" :items="[3,4,5]"/>
    <p @click="test" ref="p">1213click</p>
    <!-- <Card title="abc">
      <p slot="title">The standard card</p>
      <p>Content of card</p>
      <p>Content of card</p>
      <p>Content of card</p>
    </Card> -->
  <Modal v-model="a" title="用户" width="600">
    <Table :columns="columns" :data='data'></Table>
  </Modal>
  <pre class="test-pre" ref="pre">{{ data }}</pre>
  </div>
</template>

<script>
import test from '@/components/test'
export default {
  components: {
    test
  },
  data () {
    return {
      a: true,
      value1: [1,2],
      columns: [],
      data: [{
        name: 'huashi',
        age: 22,
        ser: 1
      }]
    }
  },

  methods: {
    test(){
      /* this.$refs.test.test() */
      console.log(this.$refs.test, this.$refs.p)
      console.log(this.$refs.pre.innerHTML)
      // this.a = true
      // console.log(this.$children)
      // this.$children[1].parentTrigger(33)
    }
  }
}
</script>

<style scoped>
.test-pre{
  background: #ccc;
  padding: 24px;
  text-align: left;
}
</style>


